import React, { useState, useEffect, useMemo } from 'react'
import SubHeaderCrumbs from '../CommonComponents/SubHeader'
import { useDispatch, useSelector } from 'react-redux'
import { Button, Card, Col, Container, Dropdown, Form, OverlayTrigger, Row, Tooltip } from 'react-bootstrap'
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper'
import EmptyWithIcon from '../CommonComponents/Empty/EmptyWithIcon'
import { useNavigate } from 'react-router-dom'
import AddContentModal from '../CommonComponents/ContentSwitcher/AddContentModal'
import { GALLERY_WALL_UPDATE } from '../../../../actions/sceneActions'
import { Loader } from '../CommonComponents/Loader'
import { shopifyApp, SWAL_FIRE, switchWarning } from '../../../../utils'
import CustomToolTip from '../CommonComponents/CustomToolTip'
import Author from '../CommonComponents/Author/Author'
import queryString from 'query-string'
import NetworkIconsSection from '../CommonComponents/NetworkIconsSection'
import { FILTER_LOADER_UPDATE, UPDATE_DASHBOARD_DATA } from '../../../../reducer/keys'
import { GET_FEEDS } from '../../../../actions/manageFeedsAction'
import { checkPermission } from '../../../../actions/collaboratorActions'
import SortBy from '../CommonComponents/SortBy'
import { GET_ALL_WEBSITE_LIST } from '../../../../actions/websiteAction'

function Content() {
    const [toggleG, setToggleG] = useState(false)
    const { wallsLoader } = useSelector(state => state?.dashboardData)
    const [editContent, setEditContent] = useState([]);
    const [spinner, setSpinner] = useState(false)
    const [apiError, setApiError] = useState(false)
    const allWalls = useSelector(state => state.dashboardData?.dashboarddata?.allWalls)
    const webList =  useSelector(state => state.websiteList?.websiteList)
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const { content, breadCrumbs } = useMemo(() => {
        const appData = window?.APP_CONTENT ?? {}
        return {
            content: appData?.['content_gallery'] ?? {},
            breadCrumbs: appData?.['sideBar'] ?? {}
        }
    }, [])
    const isShopify = shopifyApp();

    const addGallery = () => setToggleG(true);

    const handleEdit = (item) => {
        setToggleG(true)
        setEditContent(item)
    }

    const callback = (msg) => {
        setSpinner(false);
        if (msg) setApiError(msg)
        else {
            setToggleG(false);
            setEditContent([])
        }
    }

    const redirectToPublish = (item) => event =>  {
        event.stopPropagation();
        const { wallId, webId } = webList[0]?.wall?.ThemeInfo ?? { wallId: 0, webId: 0 };
        if(item?.id == wallId) navigate(`/website/publish/${webId}`)
        else navigate(`/publish/${item?.id}`);
    }

    const deleteContent = (item) => {
        const commonText = window?.APP_CONTENT?.common ?? {};
        SWAL_FIRE({
            title: commonText?.are_you_sure ?? "",
            text: content?.pop?.msg_delete ?? "",
            icon: "warning",
            showCancelButton: true,
            cancelButtonText: commonText?.cancel ?? "",
            confirmButtonText: commonText?.yes_delete ?? "",
            confirmButtonAriaLabel: "delete_yes",
            cancelButtonAriaLabel: "delete_no"
        }).then((result) => {
            if (result.value) {
                dispatch(GALLERY_WALL_UPDATE({ name: item.name, status: item.status, autoUpdate: item.autoUpdate || 0, id: item.id, deleted: 0, action: 3 }, null, callback))
            }
            else if (result.dismiss === "cancel") { }
        });
    }

    // const isGalleryNameExist = (galleryName) => allWalls?.some(wall => wall?.name?.toLowerCase() === galleryName?.toLowerCase());

    // const onAddGallery = (name, autoUpdate, status) => {
    //     const galleryName = name?.trim();

    //     if (isGalleryNameExist(galleryName)) {
    //         setApiError("Gallery name already exists.");
    //         return;
    //     }

    //     if (galleryName) {
    //         setSpinner(true);
    //         dispatch(GALLERY_WALL_UPDATE({ name: galleryName, status, carouselPost: 0, autoUpdate: autoUpdate || 0, deleted: 0, id: '', action: 1 }, navigate, callback));
    //     }
    // };

    const onUpdate = (id, galleryName, status, autoupdate, carouselPost) => {
        if (galleryName && galleryName.length > 0) {
            setSpinner(true)
            dispatch(GALLERY_WALL_UPDATE({ name: galleryName?.trim(), carouselPost: carouselPost || 0, status: status, id: id, autoUpdate: autoupdate || 0, deleted: 0, action: 2 }, null, callback))
        }
    }

    const changeToggles = (data) => {
        const [item, type, val] = data
        dispatch(GALLERY_WALL_UPDATE({ name: item.name, carouselPost: item.carouselPost || 0, status: type == 'status' ? val : item.status, autoUpdate: type == 'auto' ? val : item.autoUpdate || 0, id: item.id, deleted: 0, action: 2 }, null, null, true))
    }

    const openGallery = (id, feed, posts) => {
        localStorage.setItem('wallId', id)
        if (feed || posts) {
            //dispatch({ type: FILTER_LOADER_UPDATE, payload: true });
            dispatch(GET_FEEDS());
            setTimeout(() => {
                navigate(`/content/moderation/${id}`)
            }, 100);
        } else navigate(`/content/addfeed/${id}`)
    }

    const sortData = (data) => {
        dispatch({ type: UPDATE_DASHBOARD_DATA, payload: data });
    }

    useEffect(() => {
        const result = queryString.parse(window.location.search);
        if (result && result.addGallery) setToggleG(true)
        if(!webList?.length) dispatch(GET_ALL_WEBSITE_LIST(true))
    }, [])

    return <>
        <SubHeaderCrumbs
            crumbs={[
                { name: breadCrumbs?.contentGallery, link: "/content", icon: "image" },
                { name: breadCrumbs?.home, link: "/home" },
                { name: breadCrumbs?.contentGallery, link: "/content" }
            ]} crumbIcon="image" />


        <Container className={isShopify ? "" : "container-content-gallery"} style={{ maxWidth: '100%' }}>
            <Row className={`mb-4 mt-5 ${isShopify ? 'mt-5' : ''}`}>
                <Col md={6}>
                    {/* <h2 className="h4 mb-1">Content</h2>
                    <p className="fs-8 text-muted mb-0">Choose the Gallery to manage your content</p> */}
                    <h2 className="h4 mb-1">{content?.heading ?? ""}</h2>
                    <p className="fs-8 text-muted mb-0">{content?.sub_heading ?? ""}</p>
                </Col>
                <Col md={6} className='mt-md-0 mt-3'>
                    <div className="d-flex justify-content-md-end mb-3">
                        {allWalls?.length > 10 ? <SortBy {...{ action: sortData, data: allWalls, text: content?.filter }} /> : null}
                        <Button style={{ backgroundColor: 'var(--bs-body-bg)' }} size="md" variant="secondary" aria-label='Add Channel' onClick={() => checkPermission('contentGallery', 'createGallery', true) ? addGallery() : {}}>
                            <FontAwesomeIcon type={'solid'} icon={"plus"} /> {content?.btn ?? ""}
                        </Button>
                    </div>
                </Col>
            </Row>
            <div className='w-100 sync_products'>
                {wallsLoader ? <Loader />
                    : allWalls?.length ?
                        <Row className='mx-0 mx-n3'>
                            {allWalls.map((item, index) => {
                                return (
                                    <Col xl={3} lg={4} md={6} sm={6} key={index} className="gutter-b" data-wall-id={item?.id}>
                                        <Card id={`${item.icon}_btn_`} className='publish-card h-100 cursor-pointer' onClick={(e) => !e.target.closest('.dropdown') ? openGallery(item?.id, item?.totalFeed, item?.totalPost) : {}}>
                                            <div className="position-absolute top-0 end-0 mt-3 me-2" >
                                                <Dropdown>
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>{content?.card?.options??"Options"}</Tooltip>}>
                                                        <Dropdown.Toggle variant='secondary' aria-label='more' className="arrow_disabled dropdown-toggle border-0 d-inline-flex py-1 px-3">
                                                            <FontAwesomeIcon icon={'ellipsis-vertical'} size={20} iconClass={'me-0'} />
                                                        </Dropdown.Toggle>
                                                    </OverlayTrigger>
                                                    <Dropdown.Menu>
                                                        <Dropdown.Item onClick={() => handleEdit(item)} className={'fs-8'}>
                                                            <span className='me-2'><FontAwesomeIcon type={'solid'} icon={'edit'} /></span>
                                                            {content?.pop?.rename??"Rename"}
                                                        </Dropdown.Item>
                                                        <Dropdown.Item onClick={redirectToPublish(item)} className={'fs-8'}>
                                                            <span className='me-2 fs-8'><FontAwesomeIcon type={'solid'} icon={'paper-plane'} /></span>
                                                            {content?.pop?.publish??"Publish"}
                                                        </Dropdown.Item>
                                                        <Dropdown.Item onClick={() => deleteContent(item)} className={'fs-8'}>
                                                            <span className='me-2'><FontAwesomeIcon type={'solid'} icon={'trash-can'} /></span>
                                                            {content?.pop?.delete??"Delete"}
                                                        </Dropdown.Item>
                                                        <div className="dropdown-item d-flex justify-content-between align-items-center" onClick={(e) => e.stopPropagation()}>
                                                            <Form.Label htmlFor='cont_a_u' className="mb-0 fs-8">{content?.pop?.auto_update??"Auto update"}</Form.Label>
                                                            <Form.Check type="switch" id="cont_a_u" checked={item?.autoUpdate}
                                                                onChange={() => item?.autoUpdate ? switchWarning(content?.pop?.msg_auto_update_off ?? "", changeToggles, [item, 'auto', Number(!item?.autoUpdate)]) : changeToggles([item, 'auto', Number(!item?.autoUpdate)])}
                                                            />
                                                        </div>
                                                        <div className="dropdown-item d-flex justify-content-between align-items-center" onClick={(e) => e.stopPropagation()}>
                                                            <Form.Label htmlFor='cont_s' className="mb-0 fs-8">{content?.pop?.status??"Status"}</Form.Label>
                                                            <Form.Check type="switch" id="cont_s" checked={item?.status}
                                                                onChange={() => item?.status ? switchWarning(content?.pop?.msg_status_off ?? "", changeToggles, [item, 'status', Number(!item?.status)]) : changeToggles([item, 'status', Number(!item?.status)])}
                                                            />
                                                        </div>
                                                    </Dropdown.Menu>
                                                </Dropdown>
                                            </div>
                                            <Card.Body className='flex-column f-center px-3 py-0'>
                                                <div className="d-flex w-100 flex-wrap">
                                                    <div className='d-flex align-items-center'>
                                                        <Author imageSize={40} symbolClass={'symbol-label'}
                                                            className={'symbol symbol-54 fs-2 rounded-2 flex-shrink-0 me-2 overflow-hidden'}
                                                            name={item.name} profileUrl={item.thumbnail} networkId={28} />
                                                        <div className="publish-detail d-flex flex-column ms-2">
                                                            <strong className="h4 mb-0 pe-3 custom-truncate-1 text-capitalize">{item?.name}</strong>
                                                            <span className='fs-9 text-capitalize'>{item?.totalPost} {content?.card?.post} | {item?.totalFeed || 0} {content?.card?.feed}</span>
                                                        </div>
                                                    </div>
                                                    <div className={`content-foot mt-5 align-items-center d-flex justify-content-between w-100`}>
                                                        {item?.networkIds?.length ? <NetworkIconsSection networkList={item?.networkIds} /> :
                                                            <div>
                                                                <span className='me-2 btn btn-sm bg-secondary border'>
                                                                    <FontAwesomeIcon type={'regular'} iconClass={'me-0'} icon={'grid-2-plus'} />
                                                                    <span className="ms-2 ">{content?.card?.add_feeds}</span>
                                                                </span>
                                                            </div>
                                                        }
                                                        <div>
                                                            <CustomToolTip cssClName="light" overlayText={content?.card?.edit??"Edit"}>
                                                                <span onClick={(e) => openGallery(item?.id, item?.totalFeed)} className='me-2 btn btn-sm bg-secondary border'>
                                                                    <FontAwesomeIcon type={'solid'} iconClass={'me-0'} icon={'edit'} />
                                                                </span>
                                                            </CustomToolTip>
                                                            <CustomToolTip cssClName="light" overlayText={content?.card?.publish??"Publish"}>
                                                                <span onClick={redirectToPublish(item)} className='me-2 btn btn-sm bg-secondary border'>
                                                                    <FontAwesomeIcon type={'solid'} iconClass={'me-0'} icon={'paper-plane'} />
                                                                </span>
                                                            </CustomToolTip>
                                                        </div>
                                                    </div>
                                                </div>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                )
                            })}
                        </Row>
                        :
                        <div className="f-center" style={{ minHeight: 'calc(100vh - 240px)' }}>
                            <EmptyWithIcon
                                title={content?.empty_msg ?? ""}
                                desc={content?.empty_sub_msg ?? ""}
                                icon={'media'}
                                button={{ text: content?.btn ?? "", action: addGallery }}
                                isFullScreen={false}
                            />
                        </div>
                }
            </div>
        </Container >

        {toggleG &&
            // <AddGallery 
            //     show={toggleG} 
            //     handleClose={() => {
            //         setToggleG(false)
            //         setIsEdit(false)
            //     }} 
            //     title={"Channel To Publish"} 
            //     navigate={navigate} 
            //     isEdit={isEdit} 
            //     editData={editData} 
            //     updateData={updateData} 
            // />
            <AddContentModal show={toggleG} apiError={apiError} setApiError={setApiError} title="Gallery" onUpdate={onUpdate} editContent={editContent} handleClose={() => { setEditContent([]); setToggleG(false); setApiError('') }} deleteContent={deleteContent} spinner={spinner} setSpinner={setSpinner} statusKey={true} callback={callback} navigate={navigate} content={content?.pop} />
        }
    </>
}
export default Content